*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

html body{
    width: 100vw;
}

body{
    overflow-x: hidden;
    margin: auto;
    height: 100%;
}

.SearchBar{
    display: flex;
    justify-content: center;
    background: none;
}

.SearchBar input{
    margin: auto;
    width: 50vw;
    padding: 1rem;
    border-color: gray;
    border-width: 1px;
    border-radius: 8px;
}

.SearchBar input[type=text]{
    font-size: 2rem;
}

.SearchBar input::placeholder{
    color: gray;
    font-size: 2rem;
    font-weight: 500;
    text-transform: capitalize;
}

.HomePage{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url('https://images.unsplash.com/photo-1507525428034-b723cf961d3e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1473&q=80');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;
}

.HomePageLogo{
    margin-top: 15%;
    margin-bottom: 1rem;
    background-color: white;
    border: black 5px solid;
    border-radius: 8px;
    padding: 1.1rem;
}

.HomePageLogo h1{
    font-size: 3.8rem;
    font-weight: bold;
    letter-spacing: .1rem;
}

.HomePageLogoMotto{
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
    text-decoration: underline;
    text-align: center;
}

.Nav{
    padding: 1rem 5rem 1rem 3rem;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: auto;
    background: #d3d3d3;
}

.Nav input {
    padding: .8rem;
}

.resultInfo{
    display: flex;
    justify-content: space-around;
    letter-spacing: .1rem;
    font-size: .7rem;
}

.SearchResults{
    width: 80%;
    margin-left: 3rem;
}

.SingleResult{
    margin: 2rem 0rem 2rem 0rem;
}

.SingleResult h1{
    font-size: 1.5rem;
    margin-bottom: .5rem;
}

.SingleResult h2{
    font-size: 1.7rem;
    margin-bottom: .5rem;
}

.SingleResult h3{
    font-size: 1.2rem;
    margin-bottom: .3rem;
}

.SingleResult a{
    font-size: 1.3rem;
}

